<template>
	<view>
		<u-navbar :is-back="true" title="我的预约" 	title-color='#fff' 
		back-icon-color='#fff' 
		:background="{ background:'#213244'}">
		</u-navbar>
		<view  v-for="(item,index) in gw" :key="index">
			<image mode='aspectFill' :src='item.logo' :lazy-load='true'>
			<view>
				<span>{{item.username}}</span>
				<span>{{item.introduce}}</span>
			</view>
			<button @click="makePhoneCall(item.telephone)">
				电话咨询
			</button>
		</view>
	</view>
</template>

<script>
	import api from '../../api/index.js'
	import container from '../../components/container/index.vue'
	import LsSwiper from '../../components/ls-swiper/index.vue'
	import uniIcons from "@/components/uni-icons/uni-icons.vue"
	import {
		AkxhHd
	} from '../../api/akxhhd.js'
	export default {
		data() {
			return {
				yy:[],
			}
		},
		onLoad(uid) {
			this.init(uid);
		},
		methods: {
			init(uid){
				this.AkxhHdlists(uid);
			},
			AkxhHdlists(uid){
				AkxhHd('/catalyzer/AkxhYy/page','GET',{'uid':uid}).then(res => {
					if (res.data.code == 200) {
						let result = res.data.result.records;
						let jl = [];
						AkxhHd('/catalyzer/AkxhGw/query-by-id','GET',{'id':result.gwId}).then(res => {
							if (res.data.code == 200) {
								let result2 = res.data.result.records;
								item.username = res.data.result.username;
								jl.push(item);
								this.yy = jl;
							}
						});
					}
				})
			}
		}
	}
</script>

